.contact {
  margin-block: 80px 10px;
  padding-top: 10px;
  background-image: url('../../img/icons/decor/bg-gradient.svg');
  background-size: 90% 90%;
  background-repeat: no-repeat;
  background-position: center center;
  height: max-content;
  @media (max-width: @mobile-width) {
    margin-block: 50px 50px;
    background-position: center -10%;
  }
}
.contact__title {
  text-align: center;
}
.contact__content {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @media (max-width: @laptop-width) {
    gap: 30px calc(2% - 10px);
  }
}
.contact__description {
  width: 600px;
  font-size: 32px;
  @media (max-width: 1200px) {
    text-align: center;
    padding-bottom: 20px;
  }
  @media (max-width: @laptop-width) {
    text-align: center;
    font-size: 24px;
    padding-bottom: 10px;
  }
  @media (max-width: @mobile-width) {
    width: 90vw;
    font-size: 20px;
  }
}
.contact__form {
  width: 500px;
  padding: 60px 40px;
  display: flex;
  flex-direction: column;
  & label {
    margin-bottom: 40px;
  }
  @media (max-width: 600px) {
    width: 400px;
  }
  @media (max-width: @mobile-width) {
    width: 280px;
    padding: 50px 30px 50px 20px;
  }
}
.contact__label {
  width: 100%;
  padding: 20px 5px 15px;
  border: 2px solid @color-purple;
  border-radius: 10px;
  position: relative;
  @media (max-width: @mobile-width) {
    padding: 10px 5px 8px;
  }
  &::after {
    position: absolute;
    content: "Имя";
    font-size: 16px;
    color: @color-white;
    background-color: @color-purple;
    border-radius: inherit;
    top: -12px;
    left: 20px;
    width: max-content;
    margin-left: -10px;
    padding: 0px 10px;
    @media (max-width: @mobile-width) {
      font-size: 14px;
    }
  }
  &--en::after {
    content: "Name";
  }
  &:nth-child(2)::after {
    content: "Эл. почта";
  }
  &--en:nth-child(2)::after {
    content: "Email";
  }
  &:nth-child(3)::after {
    content: "Текст сообщения";
  }
  &--en:nth-child(3)::after {
    content: "Message";
  }
  &:hover {
    background-color: @color-purple;
  }
  & input, & textarea {
    width: 98%;
    font-size: 16px;
    cursor: pointer;
    @media (max-width: @mobile-width) {
      font-size: 14px;
    }
  }
  & input:hover {
    outline: none;
    border: none;
    margin-bottom: -2px;
    &::placeholder {
      color: @color-white;
    }
  }
  & input:focus {
    outline: none;
    border: none;
    margin-bottom: -2px;
  }
  & textarea:hover::placeholder {
    color: @color-white;
  }
  & input::placeholder, & textarea::placeholder {
    color: @color-white;
    font-size: 16px;
    opacity: 0.8;
    @media (max-width: @mobile-width) {
      font-size: 14px;
      color: @color-grey-dark;
    }
  }
}
.contact__label:hover > .contact__label {
  background-color: @color-purple;
}

.thankfull {
  margin: 100px 0 100px;
  text-align: center;
  @media (max-width: 600px) {
    display: none;
  }
}
.typing-demo {
  margin: 0 auto;
  width: 44ch;
  animation: typing 10s steps(46), blink .3s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid @color-black;
  font-size: 36px;
  @media (max-width: @laptop-width) {
    font-size: 20px;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}
