.questions {
  margin-block: 80px 100px;
  padding-inline: 10px 10px;
  text-align: center;
}
@media (max-width: @mobile-width) {
  .questions h2 {
    font-size: 36px;
    &::before, &::after {
      display: none;
    }
  }
}
