.questions__reason {
  font-size: 22px;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(4, 275px);
  grid-template-areas:
  "reason-1 reason-1   reason-2   reason-2"
  "border-1 reason-img reason-img border-2"
  "reason-3 reason-3   reason-4   reason-4";
  @media (max-width: @tablet-width) {
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas:
    "reason-1"
    "reason-2"
    "reason-img"
    "reason-3"
    "reason-4";
  }
  &--1 {
    grid-area: reason-1;
    border: 8px solid @color-blue-main;
    border-right: 4px solid @color-blue-main;
    border-radius: 100px 0px 0px 100px;
    padding: 50px 20px 60px;
    @media (max-width: @tablet-width) {
      border-bottom: 4px solid @color-blue-main;
      border-right: 8px solid @color-blue-main;
      border-radius: 100px 0px 0px 0px;
      padding: 30px 20px;
    }
  }
  &--1-border {
    grid-area: border-1;
    @media (max-width: @tablet-width) {
      display: none;
    }
  }
  &--2 {
    grid-area: reason-2;
    border: 8px solid @color-blue-main;
    border-left: 4px solid @color-blue-main;
    padding: 50px 20px 60px;
    border-radius: 0px 100px 100px 0px;
    @media (max-width: @tablet-width) {
      border-top: 4px solid @color-blue-main;
      border-left: 8px solid @color-blue-main;
      border-radius: 0px 0px 100px 0px;
      padding: 30px 20px 50px;
    }
  }
  &--2-border {
    grid-area: border-2;
    @media (max-width: @tablet-width) {
      display: none;
    }
  }
  &--img {
    grid-area: reason-img;
    margin-top: -50px;
  }
  &--3 {
    margin-top: -110px;
    grid-area: reason-3;
    padding: 60px 20px 30px;
    order: -1;
    border: 8px solid @color-blue-main;
    border-right: 4px solid @color-blue-main;
    border-radius: 100px 0px 0px 100px;
    @media (max-width: @tablet-width) {
      border-right: 8px solid @color-blue-main;
      border-bottom: 4px solid @color-blue-main;
      border-radius: 0px 100px 0px 0px;
      margin-top: -30px;
      padding: 30px 20px;
    }
  }
  &--4 {
    margin-top: -110px;
    grid-area: reason-4;
    padding: 60px 20px 30px;
    order: -1;
    border: 8px solid @color-blue-main;
    border-left: 4px solid @color-blue-main;
    border-radius: 0px 100px 100px 0px;
    @media (max-width: @tablet-width) {
      border-left: 8px solid @color-blue-main;
      border-top: 4px solid @color-blue-main;
      border-radius: 0px 0px 0px 100px;
      margin-top: 0px;
      padding: 30px 10px;
    }
  }
  & dl {
    font-size: 20px;
    @media (max-width: @laptop-width) {
      font-size: 18px;
    }
    @media (max-width: @mobile-width) {
      font-size: 16px;
    }
  }
  & dt {
    font-weight: bold;
    padding-bottom: 10px;
  }
}

.figure {
  display: flex;
  width: 100%;
  background-color: @color-blue-main;
  margin-bottom: 110px;
  &__left {
    width: 66%;
    background-color: @color-white;
    border-radius: 0px 80px 80px 0px;
  }
  &__left-2 {
    width: 30%;
    background-color: @color-white;
    border-radius: 0px 80px 80px 0px;
  }
  &__center {
    width: 4%;
  }
  &__right {
    width: 30%;
    background-color: @color-white;
    border-radius: 80px 0px 0px 80px;
  }
  &__right-2 {
    width: 66%;
    background-color: @color-white;
    border-radius: 80px 0px 0px 80px;
  }
  @media (max-width: @tablet-width) {
    display: none;
  }
}
