.img {
  @media (max-width: 1200px) {
    width: 190px;
  }
  @media (max-width: 950px) {
    width: 180px;
  }
  @media (max-width: 540px) {
    width: 165px;
  }
}
.img__logo {
  margin-top: -6px;
  @media (max-width: @laptop-width) {
    height: 60px;
  }
  @media (max-width: @tablet-width) {
    height: 70px;
  }
}
.img__reason {
  width: 90px;
  height: 90px;
  @media (max-width: 1200px) {
    width: 70px;
    height: 70px;
  }
  @media (max-width: @laptop-width) {
    width: 60px;
    height: 60px;
  }
  @media (max-width: @mobile-width) {
    width: 50px;
    height: 50px;
  }
}
.img__reason-mac {
  width: 550px;
  height: 454px;
  @media (max-width: 1200px) {
    width: 450px;
    height: 372px;
  }
  @media (max-width: @laptop-width) {
    width: 400px;
    height: 330px;
  }
  @media (max-width: @mobile-width) {
    width: 300px;
    height: 248px;
  }
}
