.header {
  contain: layout;
}
.header__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

}
.header__logo {
  margin-top: -10px;
  padding-left: 3%;
  @media (max-width: @laptop-width) {
    padding-left: 3%;
  }
}
.header__menu {
  font-size: 20px;
  @media (max-width: @laptop-width) {
    font-size: 18px;
    padding-left: -20px;
  }
}
.header__list {
  display: flex;
  gap: 30px;
  @media (max-width: @laptop-width) {
    gap: 20px;
  }
}
.header__item a {
  position: relative;
  display: block;
}

.header__item--link:hover::after {
  position: absolute;
  content: url("../../img/icons/arrow.svg");
  top: 18px;
  left: 38%;
}
.header__folder {
  background-color: @color-black;
  padding: 25px 40px 20px;
  border-radius: 40px 10px 0px 0px;
  margin-bottom: -5px;
  @media (max-width: @tablet-width) {
    display: none;
  }
}

/* BLACK block*/
.header__main {
  background-color: @color-black;
  height: max-content;
  border-radius: 10px 0px 10px 10px;
  padding: 150px 60px;
  margin-bottom: 10px;
  margin-top: -8px;
  @media (max-width: @laptop-width) {
    padding: 60px 50px;
  }
  @media (max-width: @tablet-width) {
    padding: 50px 30px;
    min-height: 50vh;
  }
  @media (max-width: @mobile-width) {
    padding: 60px 30px;
    height: max-content;
  }
}
@media (max-width: @mobile-width) {
  .header__main-card {
    padding: 90px 30px 30px;
  }
  .header__main-hobby {
    padding: 30px;
  }
}
.header__privacy {
  @media (max-width: @laptop-width) {
    padding: 70px 50px 110px;
  }
  @media (max-width: @tablet-width) {
    padding: 80px 20px 30px;
  }
  @media (max-width: @mobile-width) {
    padding: 60px 30px;
  }
}

.header__container-main {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 80px;
  @media (max-width: @laptop-width) {
    flex-wrap: wrap;
    gap: 30px;
  }
}

.header__photo {
  width: 385px;
  height: 476px;
  background-image: url('../../img/okuzminova-770x952-x2.jpg');
  background-repeat: no-repeat;
  background-size: 385px 476px;
  border-radius: 32% 47% 82% 35% / 45% 45% 80% 66%;
  will-change: border-radius, transform, opacity;
  animation-name: sliderShape;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  display: block;
  @media (max-width: @mobile-width) {
    background-image: url('../../img/okuzminova-368x476.jpg');
    width: 193px;
    height: 238px;
    background-size: 193px 238px;
  }
}

  @keyframes sliderShape {
    0%,100%{
      border-radius: 30% 70% / 5% 55%;
      transform: translate3d(0,0,0) rotateZ(0.01deg);
      }
      34%{
        border-radius: 40% 84% / 3% 3%;
      transform:  translate3d(0,5px,0) rotateZ(0.01deg);
      }
      67%{
      border-radius: 40% 40% / 40% 30% ;
      transform: translate3d(0,-3px,0) rotateZ(0.01deg);
      }
  }

.header__description {
  color: @color-white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  @media (max-width: @laptop-width) {
    text-align: center;
  }
  &--position {
    text-align: center;
  }
}
.header__description--hello {
  font-size: 32px;
  color: @color-white;
  margin: 10px 0px 10px;
  font-weight: bold;
  color: @color-black;
  text-shadow:
      -1px -1px 0 @color-white,
      1px -1px 0 @color-white,
      -1px 1px 0 @color-white,
      1px 1px 0 @color-white;
  @media (max-width: @laptop-width) {
    margin-top: 25px;
    font-size: 28px;
  }
  @media (max-width: @mobile-width) {
    margin-top: 5px;
    font-size: 18px;
    line-height: 1.4;
  }
}
.header__description--job {
  font-size: 32px;
  font-weight: bold;
  padding: 20px 0px 30px;
  @media (max-width: @laptop-width) {
    font-size: 28px;
  }
  @media (max-width: @mobile-width) {
    font-size: 18px;
  }
}

