.slider-controls {
  display: none;
}
@media (max-width: 850px) {
  .slider-controls {
    display: block;
    margin-bottom: 30px;
  }
  .button__slider {
    padding: 17px 54px 15px;
  }
  .slider-controls button {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: none;
    padding: 0px;
    cursor: pointer;
    border: 4px solid @color-blue;
    background-color: @color-black;
    color: @color-black;
    &:hover {
      border: 4px solid @color-blue-main;
    }
  }
  .slider-controls .current {
    background-color: @color-blue;
    color: @color-blue;
    &:hover {
      background-color: @color-blue-main;
      color: @color-blue-main;
    }
  }
  .character-hidden {
    display: none;
  }
}
