.hobby {
  margin-block: 100px 50px;
  & h2::after {
    display: none;
  }
}
.hobby__h2 {
  margin: 20px auto;
  width: max-content;
  text-align: center;
  position: relative;
  &--yoga::before {
    position: absolute;
    content: url("../../img/icons/decor/hobby-yoga.svg");
    left: -50px;
    top: -10px;
  }
  &--bicycle::before {
    position: absolute;
    content: url("../../img/icons/decor/hobby-bicycle.svg");
    left: -60px;
    top: -10px;
  }
  &--hiking::before {
    position: absolute;
    content: url("../../img/icons/decor/hobby-hiking.svg");
    left: -50px;
    top: -10px;
  }
  &--travel::before {
    position: absolute;
    content: url("../../img/icons/decor/hobby-travel.svg");
    left: -50px;
    top: -10px;
  }
  &--knowledge::before {
    position: absolute;
    content: url("../../img/icons/decor/hobby-knowledge.svg");
    left: -50px;
    top: -10px;
  }
  &--music::before {
    position: absolute;
    content: url("../../img/icons/decor/hobby-music.svg");
    left: -70px;
    top: -10px;
  }
}
.hobby__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  text-align: center;
  color: @color-white;
  margin-bottom: 90px;
  @media (max-width: @tablet-width) {
    & .hobby__item:nth-child(7), .hobby__item:nth-child(8), .hobby__item:nth-child(9), .hobby__item:nth-child(10) {
      display: none;
    }
  }
  @media (max-width: @mobile-width) {
    & .hobby__item:nth-child(5), .hobby__item:nth-child(6){
      display: none;
    }
  }
}

.hobby__item {
  background-color: @color-black;
  border-radius: 2px 2px 12px 12px;
  padding: 30px 15px 40px;
}

.hobby__container-music {
  display: flex;
  gap: 50px;
  color: @color-white;
  @media (max-width: @laptop-width) {
    justify-content: center;
    flex-wrap: wrap;
  }
}
.hobby__item-music {
  background-color: @color-black;
  border-radius: 2px 2px 12px 12px;
  padding: 30px 15px;
  & figcaption {
    text-align: center;
  }
  @media (max-width: @laptop-width) {
    padding-bottom: 20px;
  }
}
.hobby__item-music dl {
  margin-top: 0px;
  border-radius: 2px 2px 12px 12px;
  padding: 5px;
}
.hobby__list-title {
  background-color: @color-sandy;
  color: @color-black;
  width: max-content;
  padding-inline: 10px;
  border-radius: 30px;
}
.hobby__list-music {
  padding-left: 20px;
  padding-top: 10px;
  & li {
    display: inline;
    padding-right: 16px;
    position: relative;
  }
  & li::before {
    position: absolute;
    content: "";
    width: 5px;
    height: 5px;
    border-radius: 50%;
    top: 8px;
    left: -10px;
    background-color: @color-sandy;
  }
}
.hobby__figure {
  width: 250px;
  & img {
    border-radius: 2px;
  }
  & figcaption {
    padding-top: 10px;
    width: inherit;
  }
}
