.tariff {
  margin-block: 80px 100px;
  padding-inline: 10px 10px;
  text-align: center;
}
.tariff-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px 60px;
  @media (max-width: 1350px) {
    gap: 40px 40px;
  }
  @media (max-width: 1270px) {
    gap: 60px 50px;
  }
}
.tariff-list__item {
  width: 380px;
  padding: 20px 0px;
  @media (max-width: 1270px) {
    width: 360px;
  }
  @media (max-width: @laptop-width) {
    width: 380px;
  }
  @media (max-width: @tablet-width) {
    width: 450px;
  }
}
.tariff-list__item-detail {
  position: relative;
  @media (max-width: @tablet-width) {
    padding-inline: 20px;
  }
  @media (max-width: @mobile-width) {
    padding-inline: 15px;
  }
}
.tariff-list__item-time {
  position: absolute;
  content: "";
  background-color: @color-purple-light;
  padding-inline: 20px;
  top: -28px;
  left: -22px;
  transform: rotate(-40deg);
  border-radius: 4px;
  border-radius: 25px 25px 4px 4px;
  @media (max-width: @tablet-width) {
    top: -24px;
  }
  @media (max-width: @mobile-width) {
    top: -8px;
    left: -26px;
  }
}
.tariff-list__item-include {
  font-size: 20px;
  line-height: 34px;
  padding: 20px 0px;
  border: 2px solid @color-purple;
  border-radius: 10px;
  margin: 30px 20px 60px;
  @media (max-width: @laptop-width) {
    font-size: 18px;
    line-height: 30px;
  }
  @media (max-width: @tablet-width) {
    margin: 30px 0px 50px;
  }
}
.tariff-list__information {
  position: relative;
  cursor: pointer;
}
.tariff-list__information::after {
  position: absolute;
  content: url("../../img/icons/decor/icon-information.svg");
  left: 72%;
  top: -2px;
  @media (max-width: @laptop-width) {
    left: 70%;
  }
  @media (max-width: @tablet-width) {
    left: 68%;
  }
  @media (max-width: @mobile-width) {
    left: 72%;
  }
}
.tariff-list__information--2::after {
  left: 71%;
  @media (max-width: @laptop-width) {
    left: 68%;
  }
  @media (max-width: @tablet-width) {
    left: 66%;
  }
  @media (max-width: @mobile-width) {
    left: 70%;
  }
}
.tariff-list__information--hosting:hover::after {
  content: "Размещение сайта на сервере";
  top: -70px;
  left: 70%;
  font-size: 14px;
  line-height: 20px;
  color: @color-white;
  background-color: @color-purple;
  border-radius: 20px 20px 20px 0px;
  padding: 5px 10px;
  transform: rotate(-10deg);
}
.tariff-list__information--hosting-en:hover::after {
  content: "Hosting a website on a server";
}
.tariff-list__information--domain:hover::after {
  content: "Название сайта yourpage.com";
  top: -70px;
  left: 70%;
  font-size: 14px;
  line-height: 20px;
  color: @color-white;
  background-color: @color-purple;
  border-radius: 20px 20px 20px 0px;
  padding: 5px 10px;
  transform: rotate(-10deg);
}
.tariff-list__information--domain-en:hover::after {
  content: "Name of the site yourpage.com";
}
.tariff-list__information:hover {
  background-color: @color-purple;

}
.tarif-list__disabled {
  opacity: 0.4;
}

.tariff-list__item-price {
  background-color: @color-purple;
  font-size: 42px;
  padding: 22px 0px;
  line-height: 50px;
  margin-bottom: 50px;
}
.tariff-list__item-button {
  margin-bottom: 40px;
  & p {
    margin: 0 auto;
  }
}

