.process {
  background-color: @color-black;
  padding: 100px 50px;
  height: max-content;
  border-radius: 10px;
  margin-bottom: 10px;
  color: @color-white;
  text-align: center;
  @media (max-width: @laptop-width) {
    padding: 60px 20px;
  }
  @media (max-width: @mobile-width) {
    padding: 40px 10px;
  }
}
.process__puzzle {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas:
  "puzzel-1 puzzel-1"
  "puzzel-2 puzzel-3"
  "puzzel-4 puzzel-3"
  "puzzel-4 puzzel-5"
  "puzzel-6 puzzel-5";
  background-color: @color-purple;
  @media (max-width: @tablet-width) {
    grid-template-columns: 1fr;
    grid-template-areas:
    "puzzel-1"
    "puzzel-2"
    "puzzel-3"
    "puzzel-4"
    "puzzel-5"
    "puzzel-6";
  }
  &--1 {
    grid-area: puzzel-1;
    border-bottom: 4px solid @color-black;
    position: relative;
    padding: 20px 30px 45px;
    background-image: url('../../img/icons/decor/01.svg');
    background-repeat: no-repeat;
    background-position: 20px;
    & .puzzle__description {
      padding-left: 115px;
    }
    @media (max-width: 1200px) {
      background-size: 120px 84px;
      & .puzzle__description {
        padding-left: 130px;
      }
    }
    @media (max-width: @laptop-width) {
      background-size: 90px 70px;
      & .puzzle__description {
        padding-left: 95px;
      }
    }
    @media (max-width: @mobile-width) {
      background-size: 80px 40px;
      background-position: 20px 40px;
      padding: 45px 10px 35px;
      & .text__h3 {
        padding-left: 20px;
      }
      & .puzzle__description {
        padding-left: 0px;
      }
    }
  }
  &--2 {
    grid-area: puzzel-2;
    border-right: 4px solid @color-black;
    // position: relative;
    padding: 25px 10px 40px 10px;
    background-image: url('../../img/icons/decor/02.svg');
    background-repeat: no-repeat;
    background-position: 20px;
    & .puzzle__description {
      padding-left: 160px;
    }
    @media (max-width: 1200px) {
      background-size: 120px 84px;
      & .puzzle__description {
        padding-left: 130px;
      }
    }
    @media (max-width: @laptop-width) {
      background-size: 90px 70px;
      & .puzzle__description {
        padding-left: 95px;
      }
    }
    @media (max-width: @tablet-width) {
      border-right: 0px;
      border-bottom: 4px solid @color-black;
    }
    @media (max-width: @mobile-width) {
      background-size: 80px 40px;
      background-position: 20px 40px;
      padding: 35px 10px 35px;
      & .text__h3 {
        padding-left: 20px;
      }
      & .puzzle__description {
        padding-left: 0px;
      }
    }
  }
  &--3 {
    grid-area: puzzel-3;
    position: relative;
    padding: 25px 10px 40px;
    background-image: url('../../img/icons/decor/03.svg');
    background-repeat: no-repeat;
    background-position: 20px 110px;
    & .puzzle__description {
      padding-left: 150px;
      padding-right: 20px;
    }
    @media (max-width: 1200px) {
      background-size: 120px 84px;
      & .puzzle__description {
        padding-left: 130px;
      }
    }
    @media (max-width: @laptop-width) {
      background-size: 90px 70px;
      & .puzzle__description {
        padding-left: 95px;
      }
      & .puzzle__description--2 {
        padding-left: 35px;
      }
    }
    @media (max-width: @mobile-width) {
      background-size: 80px 40px;
      background-position: 20px 40px;
      padding: 35px 10px 35px;
      & .text__h3 {
        padding-left: 20px;
      }
      & .puzzle__description, & .puzzle__description--2 {
        padding-left: 0px;
        padding-right: 0px;
      }
    }
  }
  &--4 {
    grid-area: puzzel-4;
    border-top: 4px solid @color-black;
    border-right: 4px solid @color-black;
    position: relative;
    padding: 10px 20px 40px 10px;
    background-image: url('../../img/icons/decor/04.svg');
    background-repeat: no-repeat;
    background-position: 20px;
    & .puzzle__description {
      padding-left: 150px;
    }
    @media (max-width: 1200px) {
      background-size: 120px 84px;
      & .puzzle__description {
        padding-left: 130px;
      }
    }
    @media (max-width: @laptop-width) {
      background-size: 90px 70px;
      & .puzzle__description {
        padding-left: 95px;
      }
    }
    @media (max-width: @tablet-width) {
      border-right: 0px;
    }
    @media (max-width: @mobile-width) {
      background-size: 80px 40px;
      background-position: 20px 40px;
      padding: 45px 10px 35px;
      & .text__h3 {
        padding-left: 40px;
      }
      & .puzzle__description {
        padding-left: 0px;
      }
    }
  }
  &--5 {
    grid-area: puzzel-5;
    border-top: 4px solid @color-black;
    position: relative;
    padding: 20px 20px 40px 10px;
    background-image: url('../../img/icons/decor/05.svg');
    background-repeat: no-repeat;
    background-position: 20px;
    & .puzzle__description {
      padding-left: 160px;
      padding-right: 30px;
    }
    @media (max-width: 1200px) {
      background-size: 120px 84px;
      border-right: 0px;
      & .puzzle__description {
        padding-left: 130px;
      }
    }
    @media (max-width: @laptop-width) {
      background-size: 90px 70px;
      & .puzzle__description {
        padding-left: 95px;
      }
    }
    @media (max-width: @tablet-width) {
      border-right: 0px;
    }
    @media (max-width: @mobile-width) {
      background-size: 80px 40px;
      background-position: 5px 50px;
      padding: 45px 10px 35px;
      & .text__h3 {
        padding-left: 45px;
      }
      & .puzzle__description {
        padding-left: 0px;
        padding-right: 0px;
      }
    }
  }
  &--6 {
    grid-area: puzzel-6;
    border-top: 4px solid @color-black;
    border-right: 4px solid @color-black;
    position: relative;
    padding: 10px 10px 40px 10px;
    background-image: url('../../img/icons/decor/06.svg');
    background-repeat: no-repeat;
    background-position: 20px;
    background-size: 148px 94px;
    & .puzzle__description {
      margin-left: 140px;
    }
    @media (max-width: 1200px) {
      background-size: 120px 84px;
      & .puzzle__description {
        margin-left: 130px;
      }
    }
    @media (max-width: @laptop-width) {
      background-size: 90px 70px;
      & .puzzle__description {
        margin-left: 95px;
      }
    }
    @media (max-width: @tablet-width) {
      border-right: 0px;
    }
    @media (max-width: @mobile-width) {
      background-size: 80px 40px;
      background-position: 20px 40px;
      padding: 45px 10px 35px;
      & .puzzle__description {
        margin-left: 0px;
      }
    }
  }
}
.puzzle {
  position: absolute;
  content: url("../../img/icons/decor/puzzle.svg");
  &--1 {
    top: 98%;
    left: 20%;
    @media (max-width: @tablet-width) {
      left: 16%;
    }
  }
  &--3 {
    top: 10%;
    left: -9px;
    transform: rotate(-90deg);
    @media (max-width: @tablet-width) {
      transform: rotate(0deg);
      top: -2%;
      left: 80%;
    }
  }
  &--4 {
    top: 14%;
    left: 94.5%;
    transform: rotate(90deg);
    @media (max-width: 1441px) {
      left: 93.5%;
    }
    @media (max-width: 1200px) {
      left: 92%;
    }
    @media (max-width: @laptop-width) {
      left: 91%;
    }
    @media (max-width: @tablet-width) {
      transform: rotate(0deg);
      top: -3%;
      left: 30%;
    }
  }
  &--5 {
    top: 15%;
    left: -9px;
    transform: rotate(-90deg);
    @media (max-width: @tablet-width) {
      transform: rotate(0deg);
      top: -3%;
      left: 80%;
    }
  }
  &--6 {
    top: 20%;
    left: 94.5%;
    transform: rotate(90deg);
    @media (max-width: 1441px) {
      left: 93.5%;
    }
    @media (max-width: 1200px) {
      left: 92%;
    }
    @media (max-width: @laptop-width) {
      left: 91%;
    }
    @media (max-width: @tablet-width) {
      transform: rotate(0deg);
      top: -4%;
      left: 24%;
    }
  }
}
.puzzle__description {
  font-size: 20px;
  @media (max-width: @laptop-width) {
    font-size: 16px;
  }
}
