// Colors
@color-black: #292D32;
@color-black-dark: #15171a;
@color-white: #EEEEEE;
@color-grey: #8899AA;
@color-grey-dark: #3d434a;
@color-blue-main: #4795E6;
@color-blue-dark: #135498;
@color-blue-light: #81CEEF;
@color-blue: #3EBEE7;
@color-purple: #7186EA;
@color-purple-dark: #253998;
@color-purple-light: #AFBBF4;
@color-green: #2CEBAC;
@color-green-dark: #45B08D;
@color-green-light: #87F5D1;
@color-sandy: #EECCBB;
@color-sandy-dark: #B39F96;
@color-sandy-light: #F7E3D9;

// Viewports
@laptop-width: 1025px;
@tablet-width: 768px;
@mobile-width: 425px;

// Fonts Rubik
@font-face {
  font-family: 'Rubik';
  src: local('Rubik Light'), local('Rubik-Light'),
  url('../../fonts/Rubiklight.woff2') format('woff2'),
  url('../../fonts/Rubiklight.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: optional;
}
@font-face {
  font-family: 'Rubik';
  src: local('Rubik'), local('Rubik-Regular'),
  url('../../fonts/Rubik.woff2') format('woff2'),
  url('../../fonts/Rubik.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: optional;
}
