@font-face {
  font-family: 'Rubik';
  src: local('Rubik Bold'), local('Rubik-Bold'),
  url('../../fonts/Rubikbold.woff2') format('woff2'),
  url('../../fonts/Rubikbold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: optional;
}
body {
  margin: 5px 5px 10px;
  padding: 0px;
  font-family: 'Rubik', "Arial", sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.5;
  color: @color-black;
  background-color: @color-white;
  @media (max-width: @tablet-width) {
    font-size: 16px;
  }
}

.page {
  height: 100%;
  margin: 10px;
  @media (max-width: @mobile-width) {
    margin: 5px;
  }
}

.page-body {
  display: grid;
  grid-auto-rows: min-content 1fr min-content;
  min-height: 100%;
  word-wrap: break-word;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
h1 {
  font-size: 80px;
  font-family: 'Rubik', "Arial", sans-serif;
  font-weight: 700;
  line-height: 90px;
  margin: 0;
  background: linear-gradient(29deg, @color-blue 40%, @color-purple 60%);
  color: transparent;
  background-clip: text;
  // word-wrap: break-all;
  // word-wrap: break-word;
  @media (max-width: @laptop-width) {
    font-size: 70px;
  }
  @media (max-width: @tablet-width) {
    font-size: 60px;
    line-height: 1.2;
  }
  @media (max-width: @mobile-width) {
    font-size: 42px;
    line-height: 1;
  }
}
h2 {
  font-size: 72px;
  margin: 50px 0;
  @media (max-width: @laptop-width) {
    font-size: 52px;
  }
  @media (max-width: @mobile-width) {
    font-size: 32px;
    line-height: 1;
  }
}
h2::before {
  padding-right: 10px;
  content: "{";
  font-size: 78px;
  color: @color-white;
  text-shadow:
      -3px -1px 0 @color-black,
      1px -1px 0 @color-black,
      -3px 1px 0 @color-black,
      1px 1px 0 @color-black;
  @media (max-width: @laptop-width) {
    font-size: 60px;
  }
  @media (max-width: @mobile-width) {
    font-size: 40px;
  }
}
h2::after {
  padding-left: 10px;
  content: "}";
  font-size: 78px;
  color: @color-white;
  text-shadow:
      -1px -1px 0 @color-black,
      3px -1px 0 @color-black,
      -1px 1px 0 @color-black,
      3px 1px 0 @color-black;
  @media (max-width: @laptop-width) {
    font-size: 60px;
  }
  @media (max-width: @mobile-width) {
    font-size: 40px;
  }
}
.h2__black {
  &::before {
    color: @color-black;
    text-shadow:
        -3px -1px 0 @color-white,
        1px -1px 0 @color-white,
        -3px 1px 0 @color-white,
        1px 1px 0 @color-white;
  }
  &::after {
    color: @color-black;
    text-shadow:
        -1px -1px 0 @color-white,
        3px -1px 0 @color-white,
        -1px 1px 0 @color-white,
        3px 1px 0 @color-white;
  }
  @media (max-width: @mobile-width) {
    font-size: 30px;
  }
}
a {
  text-decoration: none;
  font-weight: 500;
  color: @color-blue;
  &:hover {
    color: @color-blue-main;
  }
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
p {
  margin: 0;
}
svg {
  // fill: @color-blue;
  stroke: none;
}
dd {
  margin: 0;
}
figure {
  margin: 0px;
}
