.card {
  margin-block: 80px 100px;
  padding-inline: 10px 10px;
  background-image: url('../../img/icons/decor/bg-gradient.svg');
  background-repeat: no-repeat;
  background-position: center top;
  width: inherit;
  &-en {
    background-image: none;
  }
  @media (max-width: @laptop-width) {
    background-size: 90% 60%;
  }
  @media (max-width: @tablet-width) {
    background-size: 90% 50%;
    padding-inline: 0px;
  }
  @media (max-width: @mobile-width) {
    background-image: none;
  }
}
.card__title {
  text-align: center;
}
.card__bank {
  width: max-content;
  padding: 30px 60px;
  display: grid;
  grid-template-columns: 400px 200px;
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas:
  "name-bank logo-bank"
  "requisite requisite"
  "name date";
  @media (max-width: @tablet-width) {
    padding: 30px 30px;
    grid-template-columns: 360px 180px;
  }
  @media (max-width: @mobile-width) {
    padding: 30px 20px;
    grid-template-columns: 210px 90px;
    grid-template-areas:
    "logo-bank logo-bank"
    "requisite requisite"
    "name date";
  }
  &--1 {
    grid-area: name-bank;
  }
  &--2 {
    grid-area: logo-bank;
    text-align: center;
  }
  &--number {
    grid-area: requisite;
    padding: 20px 0px 50px;
    font-size: 26px;
    @media (max-width: @mobile-width) {
      text-align: center;
    }
  }
  &--3 {
    grid-area: name;
  }
  &--4 {
    grid-area: date;
  }
}
