.privacy {
  margin: 10px 20px;
}
.privacy h2 {
  margin-top: 70px;
  text-align: center;
  font-size: 36px;
  &::before, &::after {
    content: none;
  }
  @media (max-width: @laptop-width) {
    font-size: 32px;
    margin-top: 60px;
  }
  @media (max-width: @mobile-width) {
    font-size: 24px;
    margin-top: 50px;
    margin-bottom: 30px;
    line-height: 1.1;
  }
}
.privacy p {
  padding-bottom: 20px;
}
