.footer {
  margin: 100px 0px 10px;
  background-color: @color-black;
  padding: 30px calc(50% - 790px) 50px;
  height: max-content;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  @media (max-width: @laptop-width) {
    justify-content: start;
  }
  &__text {
    color: @color-blue;
    padding: 28px 0px 18px;
    position: relative;
    &::after {
      position: absolute;
      content: url("../../img/icons/decor/heart-60.svg");
      bottom: 16px;
    }
  }
}
.footer__content {
  padding-inline: 30px;
}
.footer h3 {
  color: @color-white;
}
.footer__item-messenger {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  justify-content: center;
  @media (max-width: @laptop-width) {
    justify-content: start;
  }
}
.footer__item-social {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  justify-content: center;
  @media (max-width: @laptop-width) {
    justify-content: start;
  }
}
.footer__logo {
  margin-left: -20px;
  margin-top: -10px;
}
