.portfolio {
  background-color: @color-black;
  padding: 100px 50px;
  height: max-content;
  border-radius: 10px;
  margin-bottom: 10px;
  color: @color-white;
  text-align: center;
  @media (max-width: @laptop-width) {
    padding: 60px 20px;
  }
  @media (max-width: @mobile-width) {
    padding: 30px 10px 70px;
  }
}
.portfolio__container {
  margin: 0 auto;
}
.portfolio__maket-container {
  margin-block: 20px 80px;
  display: flex;
  flex-wrap: wrap;
  gap: 30px calc(2% - 10px);
  justify-content: space-around;
  @media (max-width: 850px) {
    margin-block: 15px 50px;
  }
  @media (max-width: @mobile-width) {
    margin-block: 15px 50px;
  }
}
.portfolio__maket-item {
  width: 380px;
  height: 714px;
  border: 3px solid @color-black-dark;
  border-radius: 10px;
  position: relative;
  &:hover > .portfolio__maket-description {
    display: block;
  }
  @media (max-width: @mobile-width) {
    margin: 0 auto;
    width: 300px;
    height: 590px;
  }
}
.portfolio__maket-head {
  display: flex;
  gap: 10px;
  align-items: center;
  padding-inline: 15px 10px;
  background-color: @color-black-dark;
  & span {
    width: 12px;
    height: 12px;
    background-color: @color-white;
    border-radius: 50%;
  }
  & h3 {
    color: @color-white;
    margin: 10px 0px 10px 10px;
    font-size: 16px;
    font-weight: normal;
  }
}
.portfolio__maket-body {
  width: 380px;
  height: 670px;
  & img {
    object-fit: cover;
    width: 380px;
    height: 670px;
    border-radius: 0px 0px 10px 10px;
    &:hover {
      opacity: 0.4;
    }
    @media (max-width: @mobile-width) {
      width: 300px;
      height: 530px;
    }
  }
  @media (max-width: @mobile-width) {
    width: 300px;
    height: 530px;
    opacity: 0.4;
    margin: 0 auto;
  }
}
.portfolio__maket-description {
  position: absolute;
  bottom: 0px;
  width: inherit;
  height: 30%;
  background-color: @color-black-dark;
  color: @color-white;
  border-radius: 0px 0px 8px 8px;
  padding: 40px 0px;
  display: none;
  & ul li:not(:last-child) {
    padding-bottom: 5px;
  }
  @media (max-width: @tablet-width) {
    display: block;
  }
  @media (max-width: @mobile-width) {
    padding: 60px 0px 10px;
  }
}

@media (max-width: @tablet-width) {
  .portfolio__maket-body img{
    opacity: 0.7;
  }
}

.portfolio__maket-button {
  .link__btn-color(@color-black; @color-sandy; @color-sandy-dark; @color-sandy-light);
}
