.feedback {
  margin-block: 80px 100px;
  padding: 80px 10px;
  text-align: center;
  background-color: @color-purple-light;
  border-radius: 10px;
  font-size: 32px;
  @media (max-width: @laptop-width) {
    padding: 50px 10px;
  }
  @media (max-width: @mobile-width) {
    padding: 20px 10px;
  }
}
@media (max-width: @mobile-width) {
  .feedback h2 {
    font-size: 30px;
  }
}
.feedback__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  @media (max-width: @laptop-width) {
    gap: 30px calc(2% - 10px);
  }
}
.feedback__description {
  width: 70vw;
  font-size: 32px;
  @media (max-width: @laptop-width) {
    text-align: center;
    font-size: 24px;
  }
  @media (max-width: @mobile-width) {
    width: 90vw;
    font-size: 20px;
  }
}
.feedback__form {
  background-color: @color-white;
  border-radius: 10px;
  & h2 {
    &::before, &::after {
      content: none;
    }
  }
}
.feedback__form {
  width: 700px;
  padding: 60px 40px;
  display: flex;
  flex-direction: column;
  & label, & fieldset {
    margin-bottom: 40px;
  }
  @media (max-width: @laptop-width) {
    width: 600px;
  }
  @media (max-width: @tablet-width) {
    width: 500px;
    padding: 55px 30px;
  }
  @media (max-width: @mobile-width) {
    width: 280px;
    padding: 50px 32px 40px 15px;
  }
}
.feedback__label {
  width: 100%;
  padding: 20px 5px 15px;
  border: 2px solid @color-black;
  border-radius: 10px;
  position: relative;
  @media (max-width: @mobile-width) {
    padding: 10px 5px 8px;
  }
  &::after {
    position: absolute;
    content: "Имя";
    font-size: 16px;
    color: @color-white;
    background-color: @color-black;
    border-radius: inherit;
    top: -12px;
    left: 20px;
    width: max-content;
    margin-left: -10px;
    padding: 0px 10px;
    @media (max-width: @mobile-width) {
      font-size: 14px;
    }
  }
  &-en::after {
    content: "Name";
  }
  &:nth-child(2)::after {
    content: "Эл. почта";
  }
  &-en:nth-child(2)::after {
    content: "Email";
  }
  &:nth-child(3)::after {
    content: "Телефон";
  }
  &-en:nth-child(3)::after {
    content: "Phone";
  }
  &:nth-child(4)::after {
    content: "Какой тариф интересует?";
  }
  &-en:nth-child(4)::after {
    content: "What tariff are you interested?";
  }
  &:nth-child(5)::after {
    content: "Вид деятельности?";
  }
  &-en:nth-child(5)::after {
    content: "Kind of business?";
  }
  &:nth-child(6)::after {
    content: "Текст сообщения";
  }
  &-en:nth-child(6)::after {
    content: "Message";
  }
  &:hover {
    background-color: @color-black;
  }
  & input, & textarea {
    width: 98%;
    font-size: 16px;
    color: @color-black;
    cursor: pointer;
    @media (max-width: @mobile-width) {
      font-size: 14px;
    }
  }
  & input:hover, & textarea:hover {
    border: 1px solid @color-white;
    border-radius: 10px;
    outline: none;
    margin-bottom: -1px;
    color: @color-white;
  }
  & input:active, & textarea:active {
    border: 1px solid @color-white;
    border-radius: 10px;
    outline: none;
    margin-bottom: -1px;
    color: @color-white;
    outline: none;
  }
  & textarea:active {
    margin-bottom: 2px;
  }
  & input:focus, & textarea:focus {
    border: 1px solid @color-white;
    border-radius: 10px;
    outline: none;
    margin-bottom: -2px;
    color: @color-purple;
  }
  & input::placeholder, & textarea::placeholder {
    color: @color-black;
    font-size: 16px;
    opacity: 0.8;
    @media (max-width: @mobile-width) {
      font-size: 14px;
    }
  }
  & input:hover::placeholder, & textarea:hover::placeholder {
    color: @color-white;
  }
  & textarea:hover, & textarea:focus {
    margin-bottom: 1px;
  }
}
.feedback__label:hover > .feedback__label {
  background-color: @color-black;
}
.feedback__form .feedback__fieldset:hover {
  color: @color-white;
  & .feedback__radio .feedback__radio-item > span::before {
    border: 2px solid @color-white;
  }
  & .feedback__radio .feedback__radio-item > input + span::before {
    border: 2px solid @color-white;
  }
  & .feedback__radio .feedback__radio-item > input:checked + span::before {
    border: 2px solid @color-black;
    // outline: 2px solid @color-white;
    outline: none;
    background-color: @color-white;
    box-shadow: 0px 0px 0 3px @color-white;
  }
  & .feedback__radio .feedback__radio-item > input:checked:hover + span::before {
    border: 2px solid @color-black;
    outline: none;
    background-color: @color-purple;
    box-shadow: 0px 0px 0 3px @color-purple;
  }
  & .feedback__radio .feedback__radio-item > input:hover + span::before {
    border: 2px solid @color-black;
  }
}

// Стили для типа ввода radio
.feedback__radio {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 16px;
  margin: 10px 0px;
  padding-left: 10px;
  & label {
    margin-bottom: 10px;
    cursor: pointer;
  }
  & label:last-child {
    margin-bottom: 5px;
  }
  &-item > input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    & + span:hover::before {
      border: 2px solid @color-black;
      outline: 2px solid @color-purple;
      background-color: @color-purple;
    }
    & + span:active::before {
      background-color: @color-purple-light;
    }
    & + span:focus::before {
      background-color: @color-purple-light;
    }
    & + span:checked::before {
      background-color: @color-purple-light;
    }
    &:checked + span::before {
      border: 2px solid @color-white;
      // outline: 2px solid @color-black;
      outline: none;
      background-color: @color-black;
      box-shadow: 0px 0px 0 3px @color-black;
    }
    // &:checked + span:checked::before {
    //   border: 2px solid @color-purple;
    //   outline: 2px solid @color-purple;
    //   background-color: @color-purple;
    // }
    &:disabled + span:focus::before {
      background-color: #e9ecef;
    }
  }
  &-item input:hover + span {
    color: @color-purple;
  }
  &-item > span {
    display: inline-flex;
    align-items: center;
    user-select: none;
    &::before {
      content: '';
      display: inline-block;
      width: 15px;
      height: 15px;
      flex-shrink: 0;
      flex-grow: 0;
      border: 2px solid @color-black;
      border-radius: 50%;
      margin-right: 0.5em;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 50% 50%;
    }
  }
}
