.text__quote {
  font-size: 28px;
  font-weight: bold;
  width: max-content;
  margin: 0 auto;
  &::before {
    transform-origin: center center;
    content: url("../../img/icons/decor/quote.svg");
    @media (max-width: 540px) {
      content: "‘‘";
    }
  }
  &::after {
    transform-origin: center center;
    content: url("../../img/icons/decor/quote.svg");
    transform: rotate(90deg);
    @media (max-width: 540px) {
      content: "’’";
    }
  }
  @media (max-width: @laptop-width) {
    font-size: 24px;
  }
  @media (max-width: @tablet-width) {
    font-size: 22px;
  }
  @media (max-width: @mobile-width) {
    font-size: 20px;
    font-style: italic;
  }
}
.text__link {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
}
.text__description {
  font-size: 20px;
  padding: 10px 0px;
  @media (max-width: @laptop-width) {
    font-size: 16px;
  }
  @media (max-width: @mobile-width) {
    font-size: 14px;
  }
}
.text__h3 {
  font-size: 28px;
  margin-bottom: 16px;
  @media (max-width: @laptop-width) {
    font-size: 24px;
  }
  @media (max-width: @tablet-width) {
    font-size: 22px;
  }
  @media (max-width: @mobile-width) {
    width: min-content;
    text-align: center;
    margin: 0 auto;
    padding-bottom: 20px;
    line-height: 1.2;
  }
}
.text__price {
  font-size: 16px;
  padding-inline: 10px;
  @media (max-width: @tablet-width) {
    padding-inline: 5px;
  }
}
.text__price .link__arrow {
  font-size: 16px;
}
