.about {
  margin-block: 80px 100px;
  padding-inline: 10px 10px;
  background-image: url('../../img/icons/decor/bg-gradient.svg');
  background-repeat: no-repeat;
  background-position: center top;
  width: inherit;
  @media (max-width: @laptop-width) {
    background-size: 90% 60%;
  }
  @media (max-width: @tablet-width) {
    background-size: 90% 50%;
  }
  @media (max-width: @mobile-width) {
    background-repeat: repeat;
    background-position: center -10%;
  }
}
.about__title {
  text-align: center;
}
.about__description {
  max-width: 1024px;
  margin: 0 auto;
  text-align: center;
}
.about__steps {
  margin-block: 40px 80px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px 80px;
  &-item {
    width: 550px;
    padding: 0 10px 40px;
  }
  &--h3 {
    font-size: 32px;
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 1px solid @color-sandy;
    @media (max-width: @tablet-width) {
      font-size: 28px;
    }
    @media (max-width: @mobile-width) {
      font-size: 24px;
    }
  }
}
.about__steps-list {
  position: relative;
  padding-left: 25px;
  & li:not(:last-child) {
    padding-bottom: 10px;
  }
  &--marker::before {
    position: absolute;
    content: url("../../img/icons/decor/arrow.svg");
    rotate: 90deg;
    left: 3px;
  }
  &-skills li {
    display: inline;
    position: relative;
    padding-inline: 10px 6px;
  }
  &-skills li::before {
    position: absolute;
    content: "";
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: @color-sandy;
    font-size: 38px;
    top: 8px;
    left: 0px;
  }
}

/* Блок с цитатой*/
.about__motivation {
  width: 1200px;
  margin: 0 auto 150px;
  display: grid;
  grid-template-columns: repeat(6, 200px);
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas:
  "m-1 m-2    m-3    m-4    m-5    m-6"
  "m-7 m-text m-text m-text m-text m-8"
  "m-9 m-10   m-11   m-12   m-13   m-14";
  justify-content: center;
  @media (max-width: 1200px) {
    width: 950px;
    grid-template-columns: repeat(5, 190px);
    grid-template-areas:
    "m-2 m-3    m-4    m-5    m-6"
    "m-7 m-text m-text m-text  m-8"
    "m-9 m-10   m-11   m-12   m-13 ";
  }
  @media (max-width: 950px) {
    width: 540px;
    grid-template-columns: repeat(3, 180px);
    grid-template-areas:
    "m-2 m-4    m-6"
    "m-text m-text m-text"
    "m-9 m-11   m-13";
  }
  // @media (max-width: @mobile-width) {
  //   margin-left: -10px;
  // }
  // @media (max-width: @tablet-width) {
  //   width: 425px;
  //   grid-template-columns: repeat(3, 140px);
  //   grid-template-areas:
  //   "m-2 m-4    m-6"
  //   "m-text m-text m-text"
  //   "m-9 m-11   m-13";
  // }
  @media (max-width: 540px) {
    width: 330px;
    grid-template-columns: repeat(2, 165px);
    grid-template-areas:
    "m-2 m-4"
    "m-text m-text"
    "m-9 m-11";
    margin: 0 auto 50px;
  }
  &--1 {
    grid-area: m-1;
    background-color: @color-purple;
    border-radius: 60px 0 0 0;
    opacity: 0.8;
    @media (max-width: 1200px) {
      display: none;
    }
  }
  &--2 {
    grid-area: m-2;
    & img {
      object-fit: cover;
      display: block;
      border-radius: 40px 40px 60px 0;
    }
  }
  &--3 {
    grid-area: m-3;
    border: @color-sandy 5px solid;
    border-radius: 40px 40px 0 0;
    &:hover {
      background-color: @color-sandy;
    }
    @media (max-width: 950px) {
      display: none;
    }
  }
  &--4 {
    grid-area: m-4;
    & img {
      object-fit: cover;
      display: block;
      border-radius: 40px 0px 40px 0px;
    }
  }
  &--5 {
    grid-area: m-5;
    border: @color-purple 5px solid;
    border-radius: 0px 90px 0px 90px;
    opacity: 0.8;
    &:hover {
      background-color: @color-purple;
    }
    @media (max-width: 950px) {
      display: none;
    }
  }
  &--6 {
    grid-area: m-6;
    & img {
      object-fit: cover;
      display: block;
      border-radius: 40px 0px 40px 0px;
    }
    @media (max-width: 540px) {
      display: none;
    }
  }
  &--7 {
    grid-area: m-7;
    background-color: @color-blue;
    border-radius: 90px 0px 90px 0px;
    opacity: 0.8;
    @media (max-width: 950px) {
      display: none;
    }
  }
  &--text {
    grid-area: m-text;
    text-align: center;
    padding: 50px 40px;
    @media (max-width: 1200px) {
      padding: 30px 20px;
    }
    @media (max-width: @laptop-width) {
      padding: 40px 30px;
    }
    @media (max-width: 950px) {
      background-color: @color-sandy;
      border-radius: 0px 40px 0px 40px;
    }
    @media (max-width: 540px) {
      padding: 30px 10px 20px;
    }
  }
  &--8 {
    grid-area: m-8;
    background-color: @color-blue-main;
    border-radius: 0px 90px 0px 0px;
    @media (max-width: 950px) {
      display: none;
    }
  }
  &--9 {
    grid-area: m-9;
    & img {
      object-fit: cover;
      display: block;
      border-radius: 40px 0px 40px 0px;
    }
  }
  &--10 {
    grid-area: m-10;
    border: @color-purple 5px solid;
    border-radius: 0px 90px 0px 90px;
    opacity: 0.8;
    &:hover {
      background-color: @color-purple;
    }
    @media (max-width: 950px) {
      display: none;
    }
  }
  &--11 {
    grid-area: m-11;
    & img {
      object-fit: cover;
      display: block;
      border-radius: 40px 0px 0px 0px;
    }
  }
  &--12 {
    grid-area: m-12;
    background-color: @color-sandy;
    border-radius: 0px 0px 40px 0px;
    @media (max-width: 950px) {
      display: none;
    }
  }
  &--13 {
    grid-area: m-13;
    & img {
      object-fit: cover;
      display: block;
      border-radius: 0px 0px 0px 40px;
    }
    @media (max-width: 540px) {
      display: none;
    }
  }
  &--14 {
    grid-area: m-14;
    border: @color-purple 5px solid;
    border-radius: 60px 0px 60px 0px;
    opacity: 0.8;
    &:hover {
      background-color: @color-purple;
    }
    @media (max-width: 1200px) {
      display: none;
    }
  }
}
