.container {
  padding-inline: calc(50% - 838px);
  margin: 0 auto;
}
.container-header {
  padding-inline: calc(60% - 838px);
  margin: 0 auto;
}
@media (max-width: @laptop-width) {
  .container__laptop {
    margin: 0 auto;
  }
}
.container__button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}
